import React from 'react'
import { Box, Container } from '@mui/material'

export function ClientsSection() {
  return (
    <div data-aos="fade-up" data-aos-delay="200" data-aos-duration="1000">
      <Box sx={{
        padding: '5rem 0 5rem 0',
      }}>
        <Container>
          <h1>Clientes Satisfechos</h1>
          <p>En NC, nos tomamos muy en serio la satisfacción de nuestros clientes. Hemos trabajado con varias empresas y organizaciones de diferentes tamaños y en diversos países, y siempre hemos logrado superar las expectativas.</p>
        </Container>
      </Box>
    </div>
  )
}
