import { Box, Container } from '@mui/material'
import React, { useContext } from 'react'
import { ImageCard } from './components/Cards'
import { GlobalContext } from './components/context/Context'

export function CreativePortfolio() {
    const { cards_container_style } = useContext(GlobalContext)

    return (
        <Box
            sx={{
                padding: '2rem 0 5rem 0'
            }}>
            <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                <Container>
                    <h1>Cartera Creativa</h1>
                    <p>Bienvenido a nuestra cartera creativa, donde el arte se encuentra con la estrategia y la imaginación se une al impacto. En nuestra cartera, encontrarás una amplia gama de proyectos cautivadores y exitosos que han dejado su huella en el mercado. Desde campañas publicitarias emocionantes hasta diseños impactantes y estrategias digitales vanguardistas, hemos trabajado arduamente para llevar la esencia de cada marca al siguiente nivel. Estamos comprometidos a superar expectativas y a ofrecer soluciones de marketing creativas y efectivas que destaquen entre la multitud. Prepárate para ser inspirado y cautivado por nuestro trabajo y permítenos mostrar cómo podemos ayudarte a alcanzar tus objetivos de marketing.</p>
                </Container>
            </div>
            <Container sx={cards_container_style}>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <ImageCard title='Estandarte SRL' content='Descripción de Estandarte SRL asd asd asd as d' image='./assets/img/estandarte.jpg' maxWidth='350.4' aspectRatio='1' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                    <ImageCard title='Empresa 2' content='Descripción de la empresa 2 asd asd asd as d' image='./assets/img/times-square-publicidad.jpg' maxWidth='350.4' aspectRatio='1' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                    <ImageCard title='Empresa 3' content='Descripción de la empresa 3 asd asd asd as d' image='./assets/img/times-square-publicidad.jpg' maxWidth='350.4' aspectRatio='1' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <ImageCard title='Empresa 4' content='Descripción de la empresa 4 asd asd asd as d' image='./assets/img/times-square-publicidad.jpg' maxWidth='350.4' aspectRatio='1' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                    <ImageCard title='Empresa 5' content='Descripción de la empresa 5 asd asd asd as d' image='./assets/img/times-square-publicidad.jpg' maxWidth='350.4' aspectRatio='1' />
                </div>
            </Container>
        </Box>
    )
}
