import React, { useEffect } from 'react';
import { Home } from './Home';
import './assets/css/styles.css'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import AOS from 'aos';
import 'aos/dist/aos.css';
import { GlobalContextProvider, RoutesContextProvider } from './components/context/Context';
import { CreativePortfolio } from './CreativePortfolio';
import { ResponsiveAppBar } from './components/Navbar';
import { Footer } from './components/Footer';


function remove_aos_delay() {
    if (window.screen.width <= 400) {
        for (let elem of document.getElementsByClassName('aos-init')) {
            elem.removeAttribute('data-aos-delay')
        }
    }
}


export function App() {
    useEffect(() => {
        AOS.init();
        remove_aos_delay();
    }, [])

    return (
        <GlobalContextProvider>
            <RoutesContextProvider>
                <BrowserRouter>
                    <div id="Inicio"></div>
                    <ResponsiveAppBar pages={['Inicio', 'Marketing Digital', 'Diseño Gráfico', 'Video y Fotografía', 'Desarrollo Web']} />
                    <Routes>
                        <Route path='/' element={<Home />} />
                        <Route path='/creative-portfolio' element={<CreativePortfolio />} />
                    </Routes>
                    <Footer />
                </BrowserRouter>
            </RoutesContextProvider>
        </GlobalContextProvider>
    )


}