import React from 'react';
import { Hero } from './components/home/Hero'
import { MarketingSection } from './components/home/MarketingSection'
import { DesignSection } from './components/home/DesignSection'
import { VideoAndPhotoSection } from './components/home/VideoAndPhotoSection';
import './assets/css/styles.css'
import { WebSection } from './components/home/WebSection';
import { ClientsSection } from './components/home/ClientsSection';

export function Home() {
    return (
        <>
            <Hero />
            <div id="Marketing Digital">
                <MarketingSection />
            </div>
            <div id="Diseño Gráfico">
                <DesignSection />
            </div>
            <div id="Video y Fotografía">
                <VideoAndPhotoSection />
            </div>
            <div id="Desarrollo Web">
                <WebSection />
            </div>
            <ClientsSection />
        </>
    )
}