import React, { useContext } from 'react'
import { Container } from '@mui/material'
import Box from '@mui/material/Box';
import { SimpleCard } from '../Cards'
import { GlobalContext } from '../context/Context';

export function MarketingSection() {
    const { cards_container_style } = useContext(GlobalContext)

    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <Container>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <h1>Marketing Digital</h1>
                    <p>Nuestra especialidad es el marketing digital, ofrecemos soluciones personalizadas para ayudarte a alcanzar tus objetivos. Desde SEO y SEM hasta redes sociales y publicidad en línea, ponemos el poder del marketing digital a tu alcance</p>
                </div>
            </Container>
            <Container>
                <Box sx={cards_container_style}>
                    <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                        <SimpleCard title='SEO y SEM' content='Optimización para motores de búsqueda y estrategias de publicidad para lograr mayor visibilidad en línea.' />
                    </div>
                    <div data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                        <SimpleCard title='Redes Sociales' content='Estrategias efectivas de gestión de redes sociales para conectar con audiencias específicas.' />
                    </div>
                    <div data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                        <SimpleCard title='Publicidad en línea' content='La publicidad en línea más efectiva para aumentar el alcance y generar conversiones.' />
                    </div>
                </Box>
            </Container>
        </Box>
    )
}