import React, { useContext } from 'react'
import { Box, Container } from '@mui/material'
import { SimpleCard } from '../Cards'
import { GlobalContext } from '../context/Context'


export function WebSection() {
    const { cards_container_style } = useContext(GlobalContext)


    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <Container>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <h1>Desarrollo Web</h1>
                    <p>Creamos sitios web de alta calidad y completamente personalizados para satisfacer las necesidades de tu empresa. Ofrecemos una amplia variedad de servicios incluyendo diseño de sitios web, programación, alojamiento y más.</p>
                </div>
            </Container>
            <Container sx={cards_container_style}>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <SimpleCard title='Sitios web personalizados' content='Creamos un sitio web para tu empresa que se adapte perfectamente a lo que necesitas.' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                    <SimpleCard title='Seguridad del sitio web' content='Cuidamos la seguridad y la integridad de su sitio web para evitar posibles ataques.' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                    <SimpleCard title='Diseño de Responsive Web' content='Realizamos sitios web adaptativos para mejorar la experiencia de los usuarios.' />
                </div>
            </Container>
        </Box>
    )
}
