import React, { useContext } from 'react';
import { Link } from 'react-scroll'
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import { GlobalContext, RoutesContext } from './context/Context';
import { Route, Link as RouterLink, Routes } from 'react-router-dom';


export function ResponsiveAppBar({ pages = [] }) {
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const { brandName } = useContext(GlobalContext)
    const { urls } = useContext(RoutesContext)

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    return (
        <AppBar position="sticky" style={{ background: '#1a1a33', }} id='navbar'>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
                    <Typography
                        variant="h6"
                        noWrap
                        component='a'
                        sx={{
                            display: { xs: 'none', md: 'flex' },
                        }}
                    >
                        <RouterLink to={urls.home}
                            style={{
                                mr: 2,
                                fontFamily: 'monospace',
                                fontWeight: 700,
                                letterSpacing: '.3rem',
                                color: 'inherit',
                                textDecoration: 'none',
                            }}>
                            {brandName}
                        </RouterLink>
                    </Typography>

                    <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                        >
                            {pages.map((page) => (
                                <>
                                    <Routes>
                                        <Route path={urls.home} element={
                                            <MenuItem key={page} onClick={handleCloseNavMenu}>
                                                <Typography textAlign="center">
                                                    <Link activeClass="active" smooth spy to={page}>{page}</Link>
                                                </Typography>
                                            </MenuItem>
                                        } />
                                    </Routes>
                                </>
                            ))}
                            <Routes>
                                <Route path={urls.creative_portfolio} element={
                                    <MenuItem key='cretive-portfolio' onClick={handleCloseNavMenu}>
                                        <Typography textAlign="center">
                                            <RouterLink to={urls.home} style={{ textDecoration: 'none', color: 'inherit' }}>
                                                Inicio
                                            </RouterLink>
                                        </Typography>
                                    </MenuItem>
                                } />
                                <Route path={urls.home} element={
                                    <MenuItem key='cretive-portfolio' onClick={handleCloseNavMenu}>
                                        <Typography textAlign="center">
                                            <RouterLink to='/creative-portfolio' style={{ textDecoration: 'none', color: 'inherit' }}>
                                                Cartera Creativa
                                            </RouterLink>
                                        </Typography>
                                    </MenuItem>
                                } />
                            </Routes>
                        </Menu>
                    </Box>
                    <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
                    <Typography
                        variant="h5"
                        noWrap
                        component="a"
                        href="/"
                        sx={{
                            mr: 2,
                            display: { xs: 'flex', md: 'none' },
                            flexGrow: 1,
                            fontFamily: 'monospace',
                            fontWeight: 700,
                            letterSpacing: '.3rem',
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                        {brandName}
                    </Typography>
                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                        {pages.map((page) => (
                            <>
                                <Routes>
                                    <Route path={urls.home} element={
                                        <Button
                                            key={page}
                                            onClick={handleCloseNavMenu}
                                            sx={{ my: 2, color: 'white', display: 'block' }}
                                        >
                                            <Link activeClass="active" smooth spy to={page}>{page}</Link>
                                        </Button>
                                    } />
                                </Routes>
                            </>
                        ))}
                        <Routes>
                            <Route path={urls.home} element={
                                <Button
                                    key='creative-portfolio'
                                    onClick={handleCloseNavMenu}
                                    sx={{ my: 2, color: 'white', display: 'block' }}
                                >
                                    <RouterLink to={urls.creative_portfolio} style={{ color: 'inherit', textDecoration: 'none' }}>Cartera Creativa</RouterLink>
                                </Button>
                            } />
                            <Route path={urls.creative_portfolio} element={
                                <Button
                                    key='home'
                                    onClick={handleCloseNavMenu}
                                    sx={{ my: 2, color: 'white', display: 'block' }}
                                >
                                    <RouterLink to={urls.home} style={{ color: 'inherit', textDecoration: 'none' }}>Inicio</RouterLink>
                                </Button>
                            } />
                        </Routes>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar>
    );
}