import React, { createContext } from 'react'


export const GlobalContext = createContext()


export function GlobalContextProvider({ children }) {
    const brandName = 'NC'

    let cards_container_style = {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        marginTop: { xs: 'column', md: 'row' },
    }



    return (
        <GlobalContext.Provider value={{ brandName, cards_container_style }}>
            {children}
        </GlobalContext.Provider>
    )
}

export const RoutesContext = createContext()


export function RoutesContextProvider({ children }) {
    const urls = {
        home: '/',
        creative_portfolio: '/creative-portfolio',
    }

    return (
        <RoutesContext.Provider value={{ urls }}>
            {children}
        </RoutesContext.Provider>
    )
}
