import React, { useContext } from 'react'
import { Box, Container } from '@mui/material'
import { ImageCard } from '../Cards'
import { GlobalContext } from '../context/Context'


export function DesignSection() {
    const { cards_container_style } = useContext(GlobalContext)

    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                <Container>
                    <h1>Diseño Gráfico</h1>
                    <p>Nuestro equipo de diseñadores gráficos expertos te brinda soluciones imaginativas, frescas y
                        atractivas para representar tu marca de la mejor manera posible. Ofrecemos servicios
                        personalizados de diseño para diversos medios.</p>
                </Container>
            </div>
            <Container sx={cards_container_style}>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <ImageCard title='Diseño de Logotipos' content='Logo diseñado a medida para tu empresa, creativo y único.' image='./assets/img/logo-design.jpg' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                    <ImageCard title='Diseño Publicitario' content='Publicidad impresa y digital en tu empresa con un diseño llamativo.' image='./assets/img/times-square-publicidad.jpg' />
                </div>
            </Container>
        </Box>
    )
}
