import React from 'react'
import { Box, useMediaQuery } from '@mui/material'


export function Footer() {
    const query400px = useMediaQuery('(min-width:400px)');
    // const query900px = useMediaQuery('(min-width:900px)');
    let footer_img_style = { maxWidth: '30vw', aspectRatio: '1/1', objectFit: 'cover' }
    let footer_box_style = {
        backgroundAttachment: 'fixed',
        width: '100%',
        height: 'auto',
        background: 'top center no-repeat',
        backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("./assets/img/contacto-background.jpg")',
        backgroundSize: 'cover',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'row',
        alignItems: 'center'
    }
    if (!query400px) {
        footer_box_style['flexDirection'] = 'column'
        footer_img_style['maxWidth'] = '100vw'
    }
    else {
        footer_box_style['flexDirection'] = 'row'
        footer_img_style['maxWidth'] = '30vw'
    }


    return (
        <Box sx={ footer_box_style }>
            <img src="./assets/img/logo.png" alt="" style={ footer_img_style } />
            <div style={{ margin: '2.4rem 4.4rem 2.4rem 4.4rem' }} id="footer-links">
                <h1>Contáctanos</h1>
                <p>En NC, estamos aquí para responder cualquier pregunta y ayudarte a brindar la solución óptima a las necesidades de tu empresa. ¡Contáctanos hoy!</p>
                <ul>
                    <li>Teléfonos: <a href="tel:+53 55575079">+53 55575079</a>, <a href="tel:+53 58095239">+53 58095239</a></li>
                    <li>Correo electrónico: <a href="mailto:jacasmarketing@gmail.com">jacasmarketing@gmail.com</a></li>
                </ul>
            </div>
        </Box>
    )
}
