import React from 'react'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import { CardMedia } from '@mui/material';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';


export function SimpleCard({ title = 'Titulo de la card', content = 'Contenido de la card', background_color = '#4e4c6b' }) {
    const card_theme = createTheme({
        components: {
            // Name of the component
            MuiCard: {
                styleOverrides: {
                    // Name of the slot
                    root: {
                        // Some CSS
                        margin: '0 1rem 0 1rem',
                        background: background_color,
                        color: 'white'
                    },
                },
            },
        },
    });
    let cards_style = {
        minWidth: 250,
        maxWidth: 350.4,
        marginTop: { xs: '10px', md: '0' },
        marginBottom: { xs: '10px', md: '0' },
        height: { xs: 'auto', md: '100%' },
    }


    return (
        <ThemeProvider theme={card_theme}>
            <Card variant="outlined" sx={cards_style}>
                <CardHeader title={title} titleTypographyProps={{ fontFamily: "'Fraunces', serif", }}></CardHeader>
                <CardContent>
                    <Typography variant="body2">
                        {content}
                    </Typography>
                </CardContent>
            </Card>
        </ThemeProvider>
    )
}



export function ImageCard({ title = 'Titulo de la card', content = 'Contenido de la card', image = 'none', maxWidth = 500, aspectRatio = 5/3 }) {
    const card_theme = createTheme({
        components: {
            // Name of the component
            MuiCard: {
                styleOverrides: {
                    // Name of the slot
                    root: {
                        // Some CSS
                        margin: '0 1rem 0 1rem',
                        background: 'transparent',
                        color: 'white'
                    },
                },
            },
        },
    });


    return (
        <ThemeProvider theme={card_theme}>
            <Card variant="outlined" sx={{
                minWidth: 250,
                maxWidth: parseInt(maxWidth),
                marginBottom: { xs: '10px', md: '0' },
                marginTop: { xs: '10px', md: '0' },
            }}>
                <CardMedia
                    sx={{
                        aspectRatio: parseInt(aspectRatio),
                        borderRadius: '5px',
                        objectFit: 'cover',
                        objectPosition: 'center center',
                    }}
                    image={image}
                    title={title}
                />
                <CardHeader title={title} titleTypographyProps={{ fontFamily: "'Fraunces', serif", }}></CardHeader>
                <CardContent>
                    <Typography variant="body2">
                        {content}
                    </Typography>
                </CardContent>
            </Card>
        </ThemeProvider>
    )
}